.country-heading {
  display: flex;
  align-items: center;
  margin-bottom: 30px;

  color: brown;
}

.country-name {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: auto;

  font-size: 2.5rem;
  text-decoration: underline;
}

