.country-info {
  display: flex;
  font-size: 1.5rem;
  font-weight: 700;
}

.country-data {
  font-size: 1.4rem;
  font-weight: 400;
}

.country-meta {
  flex-basis: 40%;
}

.country-meta p {
  margin-bottom: 10px;
}

.flag-container {
  flex-basis: 60%;
  margin-left: 20px;
}

.flag {
  box-shadow: 1px 2px 4px 4px rgba(0, 0, 0, 0.2);
}
